//
// NEW STUFF
//
// --------------------------------------------
// --------------------------------------------
.logo__container--hp {
  position: relative;
  top: 2rem;
  left: 20px;
  z-index: 1;
  margin: 0;

  @include bp($bp-med) {
    position: absolute;
    left: 2rem;
  }
}

.logo__container {
  position: relative;
  // left: 20px;
}


.logo__link--hp,
.logo__link {
  display: block;
  color: rgba(255, 255, 255, 0.8);
  transition: transform 0.2s ease, color 0.5s 0.1s ease;
  transform: scale(1);

  &:hover {
    transform: scale(1.1);
    transition: transform 0.4s ease, color 0.1s 0.1s ease;
  }

  &:active {
    color: rgba(255, 255, 255, 0.9);
    transition: transform 0.2s ease-in-out;
    transform: scale(1);
  }
}


.logo__link {
  display: inline-block;
}


.logo__svg {
  width: 40px;
  height: auto;
}


.svg--logo--hp {
  width: 24px;
  height: auto;
  transition: all 0.2s ease;

  @include bp($bp-med) {
    width: 32px;
  }
}

// Standard logo
.svg--logo {
  width: 32px;
  height: auto;
}


// --------------------------------------------
// --------------------------------------------
//
// Page Mod
//
// --------------------------------------------
// --------------------------------------------

.page-mod {
  @include clearfix;
  width: 100%;
  // max-width: $width-med;
  margin: 0 auto;
  padding: 0 20px;

  @include bp($bp-med) {
    max-width: $width-med;
  }
}

.page-mod--hp-intro {
  margin-top: 40px;
  transition: margin-top 0.2s ease;

  @include bp($bp-med) {
    margin-top: 6rem;
  }
}


.page-intro {
  // margin: 25px 0;
  margin: 0;
  font-size: 22px;
  font-weight: 100;
  line-height: 1.325;
  color: rgba(255, 255, 255, 0.88);

  @include bp($bp-sm) {
    font-size: 30px;
    // margin-top: 75px;
  }
}


.page-text {
  margin-top: 18px;
  font-size: 20px;
  font-weight: 300;
  line-height: 1.525;
  color: rgba(255, 255, 255, 0.7);
}



.page-intro-sm {
  margin-top: 45px;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.425;
  text-align: center;
  color: #fff;
}


.section--h {
  @include display;
  position: relative;
  margin: 0 auto 60px;
  width: 100%;
  color: rgba(255, 255, 255, 0.8);
  font-size: 15px;
  text-transform: uppercase;
  letter-spacing: 1px;
  &:after {
    position: absolute;
    content: '\0020';
    width: 75px;
    height: 8px;
    background-color: rgba(255, 255, 255, 0.3);
    left: 0;
    bottom: -15px;
  }

  // &:after,
  // &:before {
  //   position: absolute;
  //   content: '\0020';
