  //   width: 20px;
  //   height: 30px;
  //   top: 10px;
  // }
  // &:after { right: -20px; }
  // &:before { left: -20px; }
}





// --------------------------------------------
// --------------------------------------------
//
// Teases
//
// --------------------------------------------
// --------------------------------------------

.teases-mod {
  width: 100%;
  margin: 30px auto 100px;
  background-color: $primary-color-drk;
}

.teases-mod--inner {
  margin: 0 auto;
  max-width: $width-med;
  padding: 100px 20px;
}


.tease-items {
  margin: 0;
  padding: 0;
  list-style: none;
}

.tease-h {
  margin: 0;
  font-weight: 300;
  font-size: 30px;
  font-family: $type-main;
}

.tease-item {
  margin-bottom: 15px;
  padding-bottom: 15px;
  border-bottom: 1px dashed rgba(0, 0, 0, 0.17);
  @include clearfix;

  &:last-child {
    border-bottom: 0;
    padding-bottom: 0;
  }
}


.tease-link {
  display: block;
  color: rgba(255, 255, 255, 0.6);
  text-decoration: none;

  &:hover {
    color: #fff;
    .tease-date { color: rgba(255, 255, 255, 0.6); }
  }
}


.tease-date {
  margin: 0;
  font-size: 11px;
  letter-spacing: 4px;
  color: rgba(255, 255, 255, 0.6);
}


.archive-tease__h {
  margin-top: 40px;
  margin-bottom: 0;
}

.archive-tease__link {
  position: relative;
  text-decoration: none;
  color: #fff;
  letter-spacing: 1px;

  &:after {
    position: absolute;
    display: inline-block;
    content: '→';
    color: rgba(255, 255, 255, 0.6);
    margin-left: 5px;
  }

  &:hover { text-decoration: underline; }
}





