// --------------------------------------------
// --------------------------------------------
//
// Icons
//
// --------------------------------------------
// --------------------------------------------

// Variables
$icon-small:      0.75rem;
$icon-medium:     1rem;
$icon-large:      1.25rem;
$icon-xlarge:     1.5rem;



.#{$prefix}icon {
  width: $icon-large;
  height: auto;
}


// Sizes
.#{$prefix}icon--small { width: $icon-small; height: $icon-small; }
.#{$prefix}icon--medium { width: $icon-medium; height: $icon-medium; }
.#{$prefix}icon--large { width: $icon-large; height: $icon-large; }
.#{$prefix}icon--xlarge { width: $icon-xlarge; height: $icon-xlarge; }










